.light-mode {
  .dots {
    li {
      &.slick-active {
        button {
          &:before {
            color: $black;
          }
        }
      }

      button {
        &:before {
          color: $black;
        }
      }
    }
  }

  .slick-next,
  .slick-prev {
    &:before {
      color: $black;
    }
  }
}

.dots {
  position: absolute;
  bottom: -25px;
  display: block;
  width: 100%;
  list-style: none;
  text-align: center;

  li {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;

    &.slick-active {
      button {
        &:before {
          opacity: 0.75;
          color: $white;
        }
      }
    }

    button {
      font-size: 0;
      line-height: 0;
      display: block;
      width: 20px;
      height: 20px;
      padding: 5px;
      cursor: pointer;
      color: transparent;
      border: 0;
      outline: none;
      background: transparent;

      &:before {
        font-family: "slick";
        font-size: 6px;
        line-height: 20px;
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        content: "•";
        text-align: center;
        opacity: 0.25;
        color: $white;
      }
    }
  }
}
